import React from 'react';
import { Link, Outlet, useParams } from '@remix-run/react';
import { redirect } from '@remix-run/router';
import type { LoaderArgs, V2_MetaFunction } from '@remix-run/node';

import { SongsData } from '~/constants/SongsData';

export const meta: V2_MetaFunction = () => {
    return [{ title: 'Author' }];
};

export const loader = async ({ params }: LoaderArgs) => {
    if (!params.author) {
        redirect('/songs');
    } else {
        console.log(SongsData[params.author])
        const songs = Object.keys(SongsData[params.author]);
        if (songs.length === 1) {
            console.log(5, `/songs/${params.author}/${songs[0]}`)
            redirect(`/songs/${params.author}/${songs[0]}`);
        }
    }
}

export default function Author() {
    const { author } = useParams();

    return (
        <div>
            <p>This is where the individual teams will appear</p>
            {Object.keys(SongsData[author!]).map((name, index) => (
                <Link to={`/songs/${author}/${name}`} key={index}>
                    <p>{name}</p>
                </Link>
            ))}
            <Outlet />
        </div>
    );
}