import React, { useEffect, useState } from 'react';
import type { V2_MetaFunction } from '@remix-run/node';
import { useParams } from '@remix-run/react';

import { getSong } from '~/api/songService';
import type { ISong } from '~/constants/SongsData';
import Lyrics from '~/components/Lyrics/Lyrics';

export const meta: V2_MetaFunction = () => {
    return [{ title: 'Song' }];
};

export default function Song() {
    const { author, song } = useParams();
    const [lyrics, setLyrics] = useState<ISong | null>(null);

    useEffect(() => {
        (async () => {
            try {
                const res = await getSong(author, song);
                setLyrics(res);
            } catch (e) {
                // goTo404();
            }
        })();
    }, [author, song]);

    console.log(author, song)
    return lyrics && author ? <Lyrics author={author} song={lyrics} /> : null;
}
