import React from 'react';
import { Link, Outlet } from '@remix-run/react';
import { AuthorsList } from '~/constants/SongsData';

export default function Songs() {
    return (
        <div>
            <h1>Author index</h1>
            <div style={{ display: 'flex', gap: '15px', width: '100%', flexFlow: 'row wrap', padding: '15px', boxSizing: 'border-box' }}>
                {AuthorsList.map((author, index) => (
                    <Link to={`/songs/${author}`} key={index}>{author}</Link>
                ))}
                <Outlet />
            </div>
        </div>
    );
}