import { useContext } from 'react';
import { NavLink } from '@remix-run/react';
import IconButton from '@mui/material/IconButton';

import Routes from '~/constants/routes';
import { Themes } from '~/constants/themes';
import { CHANGE_THEME_ACTION, ThemeContext } from '~/components/ThemeContext';
import Moon from '~/assets/Moon';
import Sun from '~/assets/Sun';

import styles from './Header.module.css';

const Header = () => {
  const {
    state: { theme },
    dispatch
  } = useContext(ThemeContext);

  const changeTheme = (theme: Themes) => dispatch({
    type: CHANGE_THEME_ACTION,
    payload: { theme }
  });

  function toggleTheme() {
    if (theme === Themes.LIGHT) {
      changeTheme(Themes.DARK);
    } else {
      changeTheme(Themes.LIGHT);
    }
  }

  return (
    <div id="header" className={styles.header}>
            <div className={styles.slots}>
                <div className={styles.slot} />
                <div className={styles.slot}>
                    <NavLink to={Routes.CORE}>Lyrics</NavLink>
                    <NavLink to={Routes.CHORDS}>Chords</NavLink>
                </div>
                <div className={styles.slot}>
                    <IconButton onClick={toggleTheme} aria-label="change theme" component="label">
                        {theme === Themes.LIGHT ? <Moon /> : <Sun />}
                    </IconButton>
                </div>
            </div>
        </div>);

};

export default Header;